@media screen and (min-width: 1200px) {
  .settings-welcome h1 {
    font-size: 2.5em;
    font-weight: 300;
  }

  .uinfo-three {
    margin: 0 auto;
    width: 1175px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-line-pack: stretch;
        align-content: stretch;
  }
  .user-info {
    margin-top: 10vh;
    margin-bottom: 5vh;
    width: 390px;
  }

  .user-info h2 {
    font-weight: 400;
    font-size: 1.5em;
    margin-bottom: 1vh;
  }

  .user-info p {
    font-size: 1em;
    margin-bottom: 1vh;
  }

  .delete-user {
    margin: 0 auto;
    width: 500px;
  }

  .delete-user h2 {
    font-weight: 400;
    font-size: 1.5em;
    margin-bottom: 1.5vh;
  }

  .delete-user p {
    font-size: 1em;
    margin-bottom: 1.5vh;
  }

  .delete-user button {
    background-color: #f08000;
    border-top-left-radius: 42px;
    border-top-right-radius: 42px;
    border-bottom-right-radius: 42px;
    border-bottom-left-radius: 42px;
    display: inline-block;
    color: #ffffff;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 15px;
    padding-right: 15px;
    text-decoration: none;
    text-align: center;
    -ms-flex-line-pack: center;
        align-content: center;
    width: 190px;
    height: 40px;
    font-size: 1em;
    font-weight: 400;
  }
}



